
  .container{

display: flex;
justify-content: center;
gap: 20;
margin-top: 20px;

  }

  .container img{
width: 200px !important;
  }


  .cart_product{
display: flex;
gap: 10px;


  }
  .btn_product{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
gap: 5px;

  }

  .btn {
padding: 5px 16px;
font-weight: bold;
cursor:pointer;
background-color: #fff;
border: none;
  }
  
  .btn {
background-color: var(--primary-color);
background-color:orange;


  }

  .container div:not(.subtotal){

    height: auto!important;
    
      }
  .cart__container{
padding: 20px;

  }
  .cart__container h2,
  .cart__container h3{
    padding: 20px;   
  }
  .cart__container hr{
margin: 10px;
    
  }
  
  .subtotal{
padding: 30px;
min-width: 300px;
height: 80%;
display: flex;
flex-direction: column;
align-items: center;
gap: 20px;
border: 1px solid rgb(193, 193, 193);
background-color: rgb(242, 241, 241);
border-radius: 5px;
  }
  .subtotal a{
text-align: center;
width: 100%;
border: none;
background-color: var(--primary-color);
border-radius: 5px;
padding: 5px 10px;
text-decoration: none;
color: black;
  }


  .subtotal a:hover{
    /* background-color: var(--primary-shade); */
    background-color: orange;

}
.subtotal,span
.subtotal >div{
  display: flex;
  gap: 20px;
}

@media screen and (max-width:968px) {
  .container{
    flex-direction: column;
    order: 1;
  }
}