*{
    margin:0;
    padding:0;
    }
    .fixed{
position: sticky;
top: 0;
z-index: 100;

    }

.header__container {
    background-color: #1a1a1a;
    display: flex;
    gap:10px;
    align-items: center;
    color: white;
    height: 70px;

}

.logo__container{
    display: flex;
    justify-content:center;
    align-items: center;
    max-width: 200px;
}

.logo__container img {
  
    width: 80%;
    margin-left: 20px;
    padding-top: 5px;
    vertical-align: middle;
}

.delivery{
    display: flex;
    justify-content:center;
    align-items: center;
    gap:3px;
    font-weight:bold;
    padding: 5px;
}

.delivery p { 
  font-size: 10px;  
}

.search{
    display: flex;
    align-items: center;
    justify-content:center;
     border-radius: 10px; /*in search borderto be curve */
    overflow:hidden;
    width:100%;
    flex:1;
}
.search select,
.search input {
    padding: 10px;
    font-weight:bold;
    outline : 1px solid white;
}
.search select{
border: none;
}

.search input {
    border: 1px solid white;
    width:100%;
    margin-right: -2px;
}

.search svg {
    padding: 7px;
    background: #febd69;
    color: #1a1a1a;
}

.search svg:hover {
   background-color: #d49644;
}
.search svg:focus-within {
    border: 2px solid rgb(235,161,173);
 }

 .order__container{
    display: flex;
    align-items: center;
    justify-content:center;
    max-width: 450px;
    gap:10px;
 }
 .order__container a {
text-decoration: none;
    color: white;
 }
 .order__container p {
font-size: 10px;
    
 }

 .language{
    width: 100px;
    display: flex;
    padding: 10px;
 }
 .language select {
    background-color: #1a1a1a;
    border: none;
    color: white;
 }
 .language img{
    width: 40%;
 }
 .cart:hover {
    color: #007bff; /* Change to your theme's highlight color */
  }
 .cart{
    position: relative;
    /* additional css */
    display: flex;
    align-items: center;
    text-decoration: none;
    color: red;
    transition: color 0.3s ease;
 }
 .cart span{
position: absolute;
font-weight: bold;
top: -6px;
font-size: 20px;
background-color: #1a1a1a;
left: 14px;
color: orange;

 }

 .order__container a:hover,
 .delivery:hover,
 .logo__container a:hover,
 .lower__container ul li:hover{
  border: 1px solid white;
  border-radius: 3px;
 }

 .lower__container{
background-color: #232f3e;
color: white;

 }

 .lower__container ul{
    display: flex;
    list-style: none;
    gap: 5px;
 }
 .lower__container ul li{
padding: 8px;
/*display: flex;*/
 }
 .lower__container ul li:first-child{
    display: flex;
    gap: 2px;
    align-items: center;
 }
  
 @media (max-width: 576px) {
    .header__container {
        display: flex;
       flex-direction: column !important;
       height: auto;
      
    }

    .lower__container ul li:not(first-child) {
    display: none;
    }

    .language {
        display: none;
       
    }
} 
